import Vue from 'vue'
import Vuex from 'vuex'
import * as getters from './getter.js';
import {state, actions, mutations} from './root.js';

Vue.use( Vuex );


export default new Vuex.Store({
  state,
  mutations,
  getters,
  actions,

  // 嚴格模式，禁止直接修改 state
  strict: false
});


