import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Home from '../container/Home.vue'
import About from '../container/About.vue'
import Contact from '../container/Contact.vue'
import Business from '../container/Business.vue'
import Partaner from '../container/Partaner.vue'
import Products from '../container/Products.vue'
import Test from '../container/Test.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/business',
      name: 'Business',
      component: Business
    },
    {
      path: '/partaner',
      name: 'Partaner',
      component: Partaner
    },
    {
      path: '/products',
      name: 'Products',
      component: Products
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    { path: '/*', redirect: '/' }
  ]
})



