// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import vuei18n from 'vue-i18n'
import store from './store'

Vue.use(Vuex);
Vue.use(vuei18n)

// i18n
import en from './i18n/en.json'; // 存放英文翻譯
import cn from './i18n/cn.json'; // 存放簡體體中文翻譯
import zh from './i18n/zh.json'; // 存放繁體中文翻譯

const locales = {
  en: en,
  zh: zh,
  cn: cn
};
// set lang from state
// lang 對應的是 locales 的 key 目前有：en, tw, cn
if (typeof (Storage) !== "undefined") {
  if (localStorage.lang) {
    store.state.lang = localStorage.lang
  } else {
    localStorage.lang = store.state.lang
  }
}

Vue.config.lang = store.state.lang; // 從 state 獲取預設語言設定。

// set locales
Object.keys(locales).forEach(function (lang) {
  Vue.locale(lang, locales[lang]);
});


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  // template: '<App/>',
  // components: { App }
  render: h => h(App)
})



