    <!--main-nav-start-->
    <div class="container-fluid">
      <ul class="main-nav">
        <!--<router-link :to="{path: '/'}">首頁</router-link>-->
        <li>
          <router-link :to="{path: '/'}">{{ $t("navbar.home") }}</router-link>
        </li>
        <li>
          <router-link :to="{path: '/about'}">{{ $t("navbar.about") }}</router-link>
        </li>
        <li>
          <router-link :to="{path: '/products'}">{{ $t("navbar.products") }}</router-link>
        </li>
        <li>
